.page {
  margin: 0;
  padding: 0;
  background-color: $color-default-white;

  @media (min-width: $desktop-width) {
    min-width: 1200px;
  }
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  font-family: $font-family-main;
  color: $basic-black;
  background-color: $color-default-white;
  margin: 0 auto;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    margin: 0 auto;
  }
}

.page__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0;
}
